@import url("https://cdn.jsdelivr.net/gh/jgthms/minireset.css@master/minireset.min.css");
@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;700&display=swap');

body, p, h1, h2, h3, h4, h5, h6, input, textarea, b, strong, em, label, button {
  font-family: 'Rubik', sans-serif;
}

:root {
  --very-dark-grey: hsl(0, 0%, 17%);
  --dark-grey: hsl(0, 0%, 59%);
}

h1, h2 {
  font-weight: 500;
}

.header {
  background: url("./images/pattern-bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
  height: 30vh;
}

.header>* {
  margin-top: 30px;
  max-width: 1300px;
}

.header-heading {
  font-size: 2rem;
}

.search-bar {
  display: flex;
  flex-wrap: nowrap;
  width: 100%;
}

.search-bar-input {
  height: 55px;
  width: 100%;
  outline: none;
  border: none;
  border-radius: 15px 0px 0px 15px;
  padding: 20px;
}

.search-bar-input:active {
  outline: none;
}

.button {
  cursor: pointer;
  background-color: var(--very-dark-grey);
  padding: 17px;
  border-radius: 0px 15px 15px 0px;
  border: none;
}

.search-info-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  padding: 35px;
  background-color: white;
  width: 70vw;
  border-radius: 15px;
  color: var(--very-dark-grey);
  -webkit-box-shadow: 5px 5px 15px 5px rgba(0, 0, 0, 0.17);
  box-shadow: 5px 5px 15px 5px rgba(0, 0, 0, 0.17);
}

.search-info-box-info {
  display: flex;
  flex-direction: column;
  margin: 20px;
}

.grey-text {
  font-size: 1rem;
  color: var(--dark-grey);
}

.leaflet-container {
  position: absolute;
  top: 30vh;
  bottom: 0px;
  left: 0px;
  right: 0px;
  width: 100vw;
  height: 70vh;
  z-index: -1;
}

.search-bar-div {
  width: 30vw;
  min-width: 320px;
}

.heading {
  font-size: 1.625rem;
  margin-top: 10px;
}

.search-info-box-info-end {
  margin: 20px;
}

.tippy-box[data-theme~='grey'] {
  border-radius: 10px;
  padding: 20px;
  background-color: lightcoral;
  color: white;
  font-family: 'Rubik', sans-serif;
}

@media only screen and (max-width: 709px) {
  .header>* {
    margin-top: 20px;
  }

  .header-heading {
    font-size: 1.25rem;
  }

  .heading {
    font-size: 1rem;
  }

  .search-bar-div {
    width: 70vw;
    min-width: 0px;
  }

  .search-info-box {
    padding: 10px;
  }

  .search-info-box-info {
    margin: 10px;
  }

  .search-info-box-info-end {
    margin: 10px;
  }

  .grey-text {
    font-size: .8rem;
  }
}